<template>
<div class="all">
   <div class="container">
      <img v-bind:src="imgUrl" @click="extraFunc"/>
      <div class="row justify-content-center align-items-center">
         <div class="seatCenter">
               <div class="row" v-if="isAdm">
                  <div class="seat seatNone"></div>
                  <div id="seat26" class="seatAdm" :class="seatClass.seat6" @click="upNdown"></div>
                  <div id="seat25" class="seatAdm" :class="seatClass.seat5" @click="upNdown"></div>
                  <div id="seat24" class="seatAdm" :class="seatClass.seat4" @click="upNdown"></div>
                  <div id="seat23" class="seatAdm" :class="seatClass.seat3" @click="upNdown"></div>
                  <div id="seat22" class="seatAdm" :class="seatClass.seat2" @click="upNdown"></div>
                  <div id="seat21" class="seatAdm" :class="seatClass.seat1" @click="upNdown"></div>
               </div>
               <div class="row">
                  <div class="col" v-if="isAdm">
                     <div class="row"><div id="seat27" class="seatAdm" :class="seatClass.seat7" @click="upNdown"></div></div>
                     <div class="row"><div id="seat28" class="seatAdm" :class="seatClass.seat8" @click="upNdown"></div></div>
                     <div class="row"><div id="seat29" class="seatAdm" :class="seatClass.seat9" @click="upNdown"></div></div>
                  </div>
                  <div class="bar" :class="[isAdm ? 'barAdm' : 'barCus']" v-on:click="admin"></div>
                  <div class="col" v-if="!isAdm">
                     <div class="row"><div id="seat19" class="seat" :class="seatClass.seat9" @click="upNdown"></div></div>
                     <div class="row"><div id="seat18" class="seat" :class="seatClass.seat8" @click="upNdown"></div></div>
                     <div class="row"><div id="seat17" class="seat" :class="seatClass.seat7" @click="upNdown"></div></div>
                  </div>
               </div>
               <div class="row" v-if="!isAdm">
                  <div id="seat11" class="seat" :class="seatClass.seat1" @click="upNdown"></div>
                  <div id="seat12" class="seat" :class="seatClass.seat2" @click="upNdown"></div>
                  <div id="seat13" class="seat" :class="seatClass.seat3" @click="upNdown"></div>
                  <div id="seat14" class="seat" :class="seatClass.seat4" @click="upNdown"></div>
                  <div id="seat15" class="seat" :class="seatClass.seat5" @click="upNdown"></div>
                  <div id="seat16" class="seat" :class="seatClass.seat6" @click="upNdown"></div>
               </div>
               <br/>
         </div>
      </div>
      <h2 class="row announce">{{announce}}</h2>
   </div>
</div>
</template>

<script>
import picPath from './../assets/homeofgeovi2.jpg';
import picPath2 from './../assets/homeofgeovi.jpg';
import jQuery from 'jquery';
import * as firebase from "firebase";
import Swal from "sweetalert2/dist/sweetalert2.min.js";
import moment from "moment";
moment.locale("zh-tw");

var config = {
    apiKey: "AIzaSyDOfQKzA2WKr50Rc1fbaVWpgAaW1ALXCSo",
    authDomain: "ttlseatdatabase.firebaseapp.com",
    databaseURL: "https://ttlseatdatabase.firebaseio.com",
    projectId: "ttlseatdatabase",
    storageBucket: "ttlseatdatabase.appspot.com",
    messagingSenderId: "775630335713"
  };
firebase.initializeApp(config);

firebase.auth().signInAnonymously().catch(function(error) {
  // Handle Errors here.
  var errorCode = error.code;
  var errorMessage = error.message;
  console.log(errorCode);
  console.log(errorMessage);
});

const db = firebase.database();

export default {
   name: 'QuerySeat',
   data() {
      return {
         isAdm: false,
         announce: '',
         imgUrl: picPath,
         adminCount: 0,
         psCount: 0,
         seatClass:{
            'seat1': 'empty',
            'seat2': 'empty',
            'seat3': 'empty',
            'seat4': 'empty',
            'seat5': 'empty',
            'seat6': 'empty',
            'seat7': 'empty',
            'seat8': 'empty',
            'seat9': 'empty'
         }
      }
   },
   methods: {
      upNdown: function(el) {
         if(!this.isAdm) return;
         var id = 'seat' + el.target.id.substr(5);
         if(this.seatClass[id] == 'empty' || this.seatClass[id] == 'emptyAdm') {
            Swal.fire({
              title: '請選擇座位狀態',
              text: "提醒：閃爍狀態半小時後會自動改為靜態",
              type: 'question',
              showCancelButton: true,
              confirmButtonColor: 'blue',
              cancelButtonColor: 'red',
              confirmButtonText: '閃爍',
              cancelButtonText: '靜態',
              footer: '<button id="reserve" class="btn btn-primary">訂位</button>',
              onBeforeOpen: () => {
                 jQuery('#reserve').click(function() {
                    db.ref('/homeofgeoviseat/' + id).set({
                       people: 'reserve',
                       mt: moment().format()
                    });
                    Swal.close()
                 });
              }
            }).then((result) => {
               if(result.dismiss && result.dismiss == 'cancel') {
                  db.ref('/homeofgeoviseat/' + id).set({
                    people: 'awhile',
                    mt: moment().format()
                 });
               } else if(result.value){
                  db.ref('/homeofgeoviseat/' + id).set({
                     people: 'arrival',
                     mt: moment().format()
                  });
               }
            });
         } else if(this.seatClass[id] == 'reserve') {
            Swal.fire({
              title: '訂位客人來了？',
              text: "若為手誤，點擊框框快空白處可取消。",
              type: 'question',
              showCancelButton: true,
              confirmButtonColor: '#3085d6',
              cancelButtonColor: '#d33',
              confirmButtonText: '取消訂位',
              cancelButtonText: '入座'
            }).then((result) => {
               if(result.dismiss && result.dismiss == 'cancel') {
                  db.ref('/homeofgeoviseat/' + id).set({
                     people: 'arrival',
                     mt: moment().format()
                  });
               } else if (result.value) {
                 db.ref('/homeofgeoviseat/' + id).set({
                   people: 'empty'
                 });
               }
            });
         } else {
            Swal.fire({
              title: '確定取消？',
              text: "確定不是手誤喔！？！？！？",
              type: 'question',
              showCancelButton: true,
              confirmButtonColor: '#3085d6',
              cancelButtonColor: '#d33',
              confirmButtonText: '對啦對啦',
              cancelButtonText: '我手滑惹'
            }).then((result) => {
              if (result.value) {
                 db.ref('/homeofgeoviseat/' + id).set({
                   people: 'empty'
                 });
              }
            });
         }
      },
      admin: function() {
         this.adminCount++;
         if(this.isAdm || this.adminCount % 5 != 0) return;
         var toAdm = this.changeToAdm;
         Swal.fire({
            title: '請輸入密碼',
            type: 'info',
            input: 'password',
            showCancelButton: true,
            confirmButtonText: '確定',
            cancelButtonText: '取消'
         }).then((result) => {
            if(result.value) {
               db.ref('/password/').once('value').then(function(snapshot) {
                  if(result.value == snapshot.val().homeofgeoviseat) {
                     toAdm();
                     Swal.fire({
                        title: '此系統已更新為iOS13',
                        text: '有問題或流程要改再跟我說',
                        imageUrl: picPath2,
                     });
                  } else {
                     Swal.fire('密碼錯惹', '幫ＱＱ', 'error');
                  }
               });
            }
         });
      },
      extraFunc: function() {
         this.psCount++;
         if(!this.isAdm || this.psCount % 5 != 0) return;
         Swal.fire({
           title: '請選擇功能',
           text: "點擊框框外空白處可取消選擇",
           type: 'question',
           showCancelButton: true,
           confirmButtonColor: 'blue',
           cancelButtonColor: 'red',
           confirmButtonText: '設定公告',
           cancelButtonText: '修改密碼'
         }).then((result) => {
            if(result.dismiss == 'cancel') {
               this.changePs();
            } else if (result.value) {
               this.changeAnnounce();
            }
         });
      },
      changePs: function() {
         Swal.fire({
            title: '輸入原密碼',
            type: 'warning',
            input: 'password',
            showCancelButton: true,
            confirmButtonText: '確認',
            cancelButtonText: '取消'
         }).then((result) => {
            if(result.value) {
               db.ref('/password/').once('value').then(function(snapshot) {
                  if(result.value == snapshot.val().homeofgeoviseat) {
                     Swal.fire({
                        title: '輸入新密碼',
                        type: 'info',
                        input: 'password',
                        showCancelButton: true,
                        confirmButtonText: '修改密碼',
                        cancelButtonText: '取消'
                     }).then((result) => {
                        if(result.value) {
                           db.ref('/password/').set({
                              homeofgeoviseat: result.value
                           });
                           Swal.fire('修改完成', '忘記密碼呼叫北極', 'success');
                        }
                     });
                  } else {
                     Swal.fire('密碼錯惹', '忘記密碼打給北極', 'error');
                  }
               });
            }
         });
      },
      changeToAdm: function(){
         this.isAdm = true;
         this.imgUrl = picPath2;
         jQuery('.all').css('background-color', 'white');
         jQuery('.announce').css('color', 'black');
         db.ref('/homeofgeoviseat/').once("value", (snapshot)=> {
            var data = snapshot.val()
            for(var id in data) {
               this.seatClass[id] = data[id].people + (this.isAdm?'Adm':'');
            }
          });
      },
      changeAnnounce: function() {
         Swal.fire({
            title: '請輸入公告或按清除公告按鈕',
            type: 'info',
            text: '點擊框框外空白處可取消選擇',
            input: 'text',
            showCancelButton: true,
            confirmButtonColor: 'blue',
            cancelButtonColor: 'red',
            confirmButtonText: '送出公告',
            cancelButtonText: '清除公告'
         }).then((result) => {
            if(result.value) {
               Swal.fire({
                  title: '確認送出公告？',
                  text: '公告：' + result.value,
                  type: 'warning',
                  showCancelButton: true,
                  confirmButtonText: '確認',
                  cancelButtonText: '取消'
               }).then((res) => {
                  if(res.value) {
                     db.ref('/announce/').set({
                         homeofgeoviseat: result.value
                     });
                     Swal.fire('公告已設定', '', 'success');
                  }
               });
            } else if (result.dismiss == 'cancel') {
               Swal.fire({
                  title: '確認清除？',
                  type: 'warning',
                  showCancelButton: true,
                  confirmButtonText: '確認',
                  cancelButtonText: '取消'
               }).then((result) => {
                  if(result.value) {
                     db.ref('/announce/').set({
                         homeofgeoviseat: ''
                     });
                     Swal.fire('公告已清除', '', 'success');
                  }
               });

            }
         });
      },
      checkStatus: function(){
         db.ref('/homeofgeoviseat/').once('value').then(function(snapshot) {
           var data = snapshot.val();
           for(var id in data) {
              if(data[id].people == 'arrival' &&
              moment().diff(moment(data[id].mt)) > 1800000) {
                db.ref('/homeofgeoviseat/' + id).set({
                    people: 'awhile',
                    mt: data[id].mt
                });
              }
           }
         });
      }
   },
   mounted() {
      db.ref('/homeofgeoviseat/').on("value", (snapshot)=> {
         var data = snapshot.val()
         for(var id in data) {
            this.seatClass[id] = data[id].people + (this.isAdm?'Adm':'');
         }
       });
       db.ref('/announce/').on("value", (snapshot)=> {
          this.announce = snapshot.val()['homeofgeoviseat'];
        });
       this.checkStatus();
       setInterval(() => {
          this.checkStatus();
       }, 30000);
   }
}
</script>

<style scoped>
.all{
   height: 100%;
   width: 100%;
   background-color: black;
   background-size: cover;
   position: absolute;
   top: 0px;
}
.seatCenter{
   margin: 0px auto;
   top: 60px;
   position: relative;
}
img{
   width: 60px;
   height: 60px;
   position: absolute;
   top: 0px;
   left: 0px;
}
.bar{
   width: 240px;
   height: 120px;
   border-width: 5px;
}
.barCus{
   border-style: none solid solid none;
   border-color: white;
}
.barAdm{
   border-style: solid none none solid;
   border-color: black;
}
.seat {
   border: 3px solid;
   border-radius: 99em;
   border-color: white;
   width: 40px;
   height: 40px;
}
.seatAdm {
   border: 3px solid;
   border-radius: 99em;
   border-color: black;
   width: 40px;
   height: 40px;
}
.seatNone {
   border-color: white;
}
.empty {
   background-color: none;
}
.arrival {
   background-color: #8B0000;
   animation-name: oxxo;
   animation-duration:2s;
   animation-iteration-count: infinite;
}
.arrivalAdm {
   background-color: red;
   animation-name: oxxoAdmin;
   animation-duration:2s;
   animation-iteration-count: infinite;
}
.awhile {
   background-color: #8B0000;
}
.awhileAdm {
   background-color: red;
}
.reserve {
   background-color: #00008B;
}
.reserveAdm {
   background-color: blue;
}
@keyframes oxxo{
   0%{
      background: #8B0000;
   }
   50%{
      background: none;
   }
   100%{
      background: #8B0000;
   }
}
@keyframes oxxoAdmin{
   0%{
      background: red;
   }
   50%{
      background: none;
   }
   100%{
      background: red;
   }
}
.announce{
   color: white;
   top: 60px;
   position: relative;
}
</style>
